<transition name="modal"><div class="modal__mask"><div class="modal__wrapper"><div class="modal__container"><div class="modal__close" @click="$emit('close')"><img src="../assets/img/icons/close.svg"></div><div class="modal__header"><div class="modal__icon"><img src="../assets/img/icons/mail.svg"></div><div class="modal__title">Подпишитесь на нашу рассылку и получите промокод на 500 RUB!</div></div><div class="modal__body"><form class="modal__form"><div class="modal__input modal__input_email"><input type="email" name="email" placeholder="E-mail"></div><input class="modal__submit" type="button" value="Войти" @click="$emit('close')"></form></div></div></div></div></transition>