<transition name="modal"><div class="modal__mask"><div class="modal__wrapper"><div class="modal__container"><div class="modal__close" @click="$emit('close')"><img src="../assets/img/icons/close.svg"></div><div class="modal__header"><div class="modal__icon"><img src="../assets/img/icons/profile-modal.svg"></div><div class="modal__title">Войти в личный кабинет</div></div><div class="modal__body"><form class="modal__form"><div class="modal__input modal__input_phone"><input type="tel" name="phone" placeholder="Телефон"></div><div class="modal__input modal__input_password"><input type="password" name="password" placeholder="Пароль"></div><a class="modal__lost">Забыли пароль?</a><input class="modal__submit" type="button" value="Войти" @click="$emit('logIn')"></form><div class="modal__register"><span>Впервые у нас? </span><a @click="$emit('showRegister')">Зарегистрироваться</a></div></div></div></div></div></transition>