<transition name="modal"><div class="modal__mask"><div class="modal__wrapper"><div class="modal__container"><div class="modal__close" @click="$emit('close')"><img src="../assets/img/icons/close.svg"></div><div class="modal__header"><div class="modal__title">Бронирование стола </div></div><div class="modal__body"><form class="modal__form"><div class="modal__input modal__input_name"><input placeholder="Имя"></div><div class="modal__cols"><div class="modal__col"><div class="modal__input modal__input_phone"><input placeholder="+380" type="tel"></div><div class="modal__input modal__input_time"><input placeholder="Время:__:__" type="time"></div></div><div class="modal__col"><div class="modal__input modal__input_count"><input placeholder="Количество человек" type="number"></div><div class="modal__input modal__input_date"><input placeholder="Дата: дд.мм.гг" type="date"></div></div></div><input class="modal__submit" type="button" value="Забронировать" @click="$emit('close')"></form></div></div></div></div></transition>