<transition name="modal"><div class="modal__mask"><div class="modal__wrapper"><div class="modal__container"><div class="modal__close" @click="$emit('close')"><img src="../assets/img/icons/close.svg"></div><div class="modal__header"><div class="modal__icon"><img src="../assets/img/icons/profile-modal.svg"></div><div class="modal__title">Регистрация</div></div><div class="modal__body"><form class="modal__form"><div class="modal__input modal__input_phone"><input type="tel" name="phone" placeholder="Телефон"></div><div class="modal__input modal__input_password"><input type="password" name="password" placeholder="Пароль"></div><div class="modal__input modal__input_password"><input type="password" name="submit-password" placeholder="Подтвердите пароль"></div><div class="modal__inner"><div class="modal__checkbox"><input type="checkbox" id="cbOffers" name="cbOffers" value="yes"><label for="cbOffers">Хочу получать выгодные предложения от магазина</label></div><div class="modal__checkbox"><input type="checkbox" id="cbPolitics" name="cbPolitics" value="yes"><label for="cbPolitics"><p>Принимаю условия <a>Пользовательского соглашения, Политики конфиденциальности</a></p></label></div></div><input class="modal__submit" type="button" value="Зарегистрироваться" @click="$emit('signIn')"></form></div></div></div></div></transition>